<template>
  <div class="sort">
    <ul>
  		<li v-for="(item,index) in sortList" @click="sortClickEvent(index)">{{item.sorts}}</li>
    </ul>
  </div>

</template>

<style lang="scss">
.sort{
    position: absolute;
    left:0;
    top:.7rem;
    overflow: hidden;
  	width: 100%;
    background: white;
    z-index: 2;
	li{
		height:.8rem;
		line-height: .8rem;
		color: #222;
		padding-left: .24rem;
		font-size: .28rem;
		border-top: 1px solid #f0f0f0;
	}
}

</style>

<script>
export default {
  data () {
    return {
      sortList: [
        {sorts:'默认排序'},
        {sorts:'最新上架'},
        {sorts:'价格最高'},
        {sorts:'价格最低'},
        {sorts:'最短里程'},
        {sorts:'最短车龄'},
      ]
    }
  },
  methods:{
    sortClickEvent(index){
      var motorcycleType = this.sortList[index].sorts;
      var False = false;
      this.$emit('sortClickEvent',motorcycleType,False);
    },
    hideSort(){
      var False = false;
      this.$emit('hideSort',False);
    }
  }
}
</script>



